import React, { PropTypes } from "react";


const Link = ({ active, children, onClick }) => {
    // if active just return a span?
    if (active) {
        return (
            <span>{children}</span>
        );
    }

    // else create an anchor tag
    return (
        <a
            href="#"
            onClick = { (e) => {
                e.preventDefault();
                onClick();
            } }
        >
            {children}
        </a>
    )

};

Link.propTypes = {
    active: PropTypes.bool.isRequired,
    children: PropTypes.node.isRequired,
    onClick: PropTypes.func.isRequired
};

export default Link;


