// Thinking in React - Product Widget
import React from "react";
import _ from "underscore";

// main product Widget
var ProductWidget = React.createClass({

    getInitialState: function () {
        return {
            filterText: '',
            inStockOnly: false
        };
    },

    // handles user input and changes the state
    handleUserInput: function (filterText, inStockOnly) {
        // change the state
        this.setState({
            filterText: filterText,
            inStockOnly: inStockOnly
        });
    },

    render: function () {

        var product_category = {};

        this.props.products.forEach( function (product, index) {

            if (!product_category[product.category]) {
                product_category[product.category] = [];
            }

            product.key = index;

            product_category[ product.category ].push(  product );
        } );

        return (
            <div className="col s12" style={{'fontSize': "15px"}}>
                <SearchBar 
                    filterText={this.state.filterText} 
                    inStockOnly={this.state.inStockOnly} 
                    onUserInput={this.handleUserInput}
                />
                <ProductTable 
                    categories={product_category} 
                    filterText={this.state.filterText}
                    inStockOnly={this.state.inStockOnly}
                />
            </div>
        );
    }

});

// search bar
var SearchBar = React.createClass({

    // calls the  parent "ProductWidget" 'onUserInput' since it owns the state of => filterText, inStockOnly
    handleChange: function () {
        this.props.onUserInput(
            this.refs.filterTextInput.value,
            this.refs.inStockOnlyInput.checked
        );
    },

    render: function () {
        return (
            <div className="col s12">
                <div className="col s12 input-field">
                    <input type="text" 
                            id="search-products"
                            placeholder="Search ..... "
                            value={this.props.filterText}
                            ref="filterTextInput"
                            onChange={this.handleChange} 
                    />
                </div>
                <div className="col s12">
                    <div>
                        <input type="checkbox" 
                                id="toggle-stock"
                                ref="inStockOnlyInput"
                                checked={this.props.inStockOnly}
                                onChange={this.handleChange}
                        />
                        <label htmlFor="toggle-stock">Show Products in Stock</label>
                    </div>
                </div>
            </div>
        );
    }

});

// Product Table showing the products
var ProductTable = React.createClass({

    render: function () {

        var self = this; // save reference

        var product_infos = [];

        _.each( this.props.categories, function (c, name, index) {

            var products = [];

            // check for filterText
            if (self.props.filterText) {
                // from the product, filter the ones which match the filter text
                products = _.filter( c, function (product) {
                    return product.name.toLowerCase().indexOf( self.props.filterText.toLowerCase() ) > -1 ? true : false;
                } );
            } else {
                products = c;
            }

            // now we have filtered products based on filterText
            // now filtering based on stock
            if (self.props.inStockOnly) {
                products = _.filter( products, function (product) {
                    return product.stocked == self.props.inStockOnly;
                } );
            }

            product_infos.push(
                <ProductInfo category={name} products={products} />
            );
        } );

        return (
            <div className="col s12">
                <div className="col s12">
                    <div className="col s6">Name</div>
                    <div className="col s6">Price</div>
                </div>

                {product_infos}  
            </div>
        );
    }

});

// ProductInfo showing information of the single product
var ProductInfo = React.createClass({

    render: function () {

        var product_details = [];

        this.props.products.forEach( function (product, index) {
            product_details.push(
                <ProductDetail name={product.name} price={product.price} key={product.id} stocked={product.stocked} />
            );
        } );

        return (
            <div className="col s12">
                <ProductHeader category={this.props.category} />
                {product_details}
            </div>
        );
    }

});

// Product Header
var ProductHeader = React.createClass({

    render: function () {
        return (
            <div className="col s12" style={{'borderBottom': 'thin solid grey', 'margin': '15px 0'}}>
                <h5>{this.props.category}</h5>
            </div>
        );
    }

});

// Product Detail
var ProductDetail = React.createClass({

    render: function () {

        var name = this.props.stocked 
                        ? this.props.name 
                        : <span style={{color: 'red'}}>{this.props.name}</span>;

        return (
            <div className="col s12">
                <div className="col s6">
                    {name}
                </div>
                <div className="col s6">
                    {this.props.price}
                </div>
            </div>
        );
    }

});

export { ProductWidget, SearchBar, ProductInfo, ProductHeader, ProductDetail };


