console.log("Porumai! building the react app");
import React from 'react';

// composing comment box
var CommentBox = React.createClass({

    // gets initial state of the component
    // note: state => state of the component and mutable, 
    // props => passed by the parent, owned by the parent, immutable
    getInitialState: function () {
        return {
            data: []
        };
    },

    // handles comment submit from its child component "CommentForm"
    handleCommentSubmit: function (comment) {

        // get a reference to the  current state
        var comments = this.state.data;
        // optimistic commenting
        comment.id = new Date().getTime();
        // add the new comment to the comments
        var updated_comments = comments.concat([ comment ]);
        // update the state without waiting for the server to return the data
        this.setState({
            data: updated_comments
        });

        // sending the details to the server
        $.ajax({
            url: this.props.url,
            type: "POST",
            dataType: "json",
            data: comment,
            success: function (data) {
                // update the saved data to state
                this.setState({
                    data: data
                });
                console.log("save successful ", data);
            }.bind(this),
            error: function (xhr, status, err) {
                // restore the old data
                this.setState({
                    data: comments
                });
                console.log("error in saving data ", this.props.url, data);
            }.bind(this)
        });
    },

    handleCommentDelete: function (comment) {
        console.log("handling comment delete upstream ", comment);

        var comments = this.state.data;

        // sending the details to the server
        $.ajax({
            url: this.props.url,
            type: "DELETE",
            dataType: "json",
            data: comment,
            success: function (data) {
                // update the saved data to state
                this.setState({
                    data: data
                });
                console.log("delete successful ", data);
            }.bind(this),
            error: function (xhr, status, err) {
                // restore the old data
                this.setState({
                    data: comments
                });
                console.log("error in deleting data ", this.props.url, comment);
            }.bind(this)
        });

    },

    // componentDidMount executes once after the component is mounted
    // and after every time the state of the component is changed
    fetchComments: function () {
        $.ajax({
            // defined on the component
            url: this.props.url,
            dataType: "json",
            cache: false,

            success: function (data) {
                this.setState({
                    data: data
                });
            }.bind(this),

            error: function (xhr, status, err) {
                console.log("error in fetching data from server", this.props.url);
            }.bind(this)
        });
    },

    componentDidMount: function () {
        // fetch comments from the server
        this.fetchComments();
        // fetch comments after regular interval
        // setInterval( this.fetchComments, this.props.pollInterval );
    },

    render: function () {
        return (
            <div className="comment-box row">
                <h1> Porumai Comments! </h1>
                <CommentList 
                    data={this.state.data}
                    handleCommentDelete={this.handleCommentDelete}
                />
                <CommentForm 
                    onCommentSubmit={this.handleCommentSubmit}
                />
            </div>
        );
    }
});

// composing comment list
var CommentList = React.createClass({

    // handles comment delete from the "Comment" component
    handleCommentDelete: function (comment) {
        console.log("will be deleting the comment from comment list ", comment);
        // send the comment delete to upstream to comment box
         this.props.handleCommentDelete( comment );
    },

    render: function () {

        var self = this; // save reference

        var comments = this.props.data.map( function (comment) {
            return (
                <Comment author={comment.author} key={comment.id} id={comment.id} onDeleteComment={self.handleCommentDelete}>
                    {comment.text}
                </Comment>
            );
        } );

        return (
            <div className="comment-list">
                {comments}
            </div>
        );
    }
});

// composing comment form
var CommentForm = React.createClass({

    getInitialState: function () {
        return {
            "author": "",
            "text": ""
        }; 
    },

    handleAuthorChange: function (e) {
        this.setState({
            "author": e.target.value
        });
    },

    handleTextChange: function (e) {
        this.setState({
            "text": e.target.value
        });
    },

    handleSubmit: function (e) {
        e.preventDefault();
        var author = this.state.author.trim(),
            text = this.state.text.trim();

        if (!author || !text) {
            console.log("No author or text given");
            return;
        }

        // saving the comments to server
        // note: prop is coming from parent
        // arg obj => onCommentsubmit handler => 
        this.props.onCommentSubmit({
            author: author,
            text: text
        });

        // we have a data
        this.setState({
            author: "",
            text: ""
        });
    },

    render: function () {
        return (
            <form className="comment-form" onSubmit={this.handleSubmit}>
                <h3>Add a Comment!</h3>
                <input 
                    type="text" 
                    placeholder="Author ..." 
                    value={this.state.author}
                    onChange={this.handleAuthorChange}
                />
                <input 
                    type="text" 
                    placeholder="Text .." 
                    value={this.state.text}
                    onChange={this.handleTextChange}
                />
                <input type="submit" value="Post!" />
            </form>
        );
    }
}); 

// single comment
var Comment = React.createClass({

    getInitialState: function () {
        return {
            id: this.props.id,
            author: this.props.author,
            text: this.props.children.toString()
        };
    },

    rawMarkup: function () {

        var md = new Remarkable(),
            raw_markup = md.render( this.props.children.toString() );

        return { __html: raw_markup };

    },

    // deleting a comment
    deleteComment: function (e) {
        e.preventDefault();
        console.log("porumai!, will be deleting ", this.state );
        // ok now we have the comment
        // we need to pass it back to the CommentBox which handles all the server connections
        // first we are passing to the CommentList
        this.props.onDeleteComment( this.state );
    },

    render: function () {

        var md = new Remarkable();

        return (
            <div className="comment row">
                <h2 className="comment-author" className="col s12">{this.props.author}</h2>
                <span dangerouslySetInnerHTML={this.rawMarkup()} className="col s10" />
                <button id="delete" className="col s2" onClick={this.deleteComment}>Delete</button>
            </div>
        );
    }
});

export { CommentBox, CommentList, CommentForm, Comment };



