import React from 'react';
import ReactDOM from 'react-dom';

import { Provider } from "react-redux";
import { createStore } from "redux";
import todoApp from "./redux/reducer";
import App from "./Components/App";



import { CommentBox, CommentForm, CommentList, Comment } from "./react-app";
import { ProductWidget, SearchBar, ProductInfo, ProductHeader, ProductDetail } from "./ProductWidget";

// import "./redux/store";

let store = createStore( todoApp );

ReactDOM.render(
    <Provider store={store}>
        <App />
    </Provider>,
    document.getElementById("todo-list")
);

// sample product details
var product_details = [
    {id: 1, category: "Sporting Goods", price: "$49.99", stocked: true, name: "Football"},
    {id: 2, category: "Sporting Goods", price: "$9.99", stocked: true, name: "Baseball"},
    {id: 3, category: "Sporting Goods", price: "$29.99", stocked: false, name: "Basketball"},
    {id: 4, category: "Electronics", price: "$99.99", stocked: true, name: "iPod Touch"},
    {id: 5, category: "Electronics", price: "$399.99", stocked: false, name: "iPhone 5"},
    {id: 6, category: "Electronics", price: "$199.99", stocked: true, name: "Nexus 7"}
];

// Thinking in React => ProductWidget
ReactDOM.render(
    <ProductWidget products={product_details} />,
    document.getElementById("product-widget")
);

// simple react message
ReactDOM.render(<h1>Hello React!</h1>, document.getElementById('root'));

// react tutorial
ReactDOM.render(
    <CommentBox 
        url="http://localhost:3000/api/comments" 
        pollInterval={12000} 
    />,
    document.getElementById("content")
);


